.container {
  width: 100%;
  height: 100%;
}

.board {
  width: 700px;
  height: 700px;
  margin: 50px auto 0;
  -webkit-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
          box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
  display: grid;
  grid-template-rows: repeat(16, 6.25%);
  grid-template-columns: repeat(16, 6.25%);
  overflow: hidden;
  position: relative;
}

.grid-line {
  -webkit-box-shadow: 0px 0px 0px 1px rgba(54, 201, 169, 0.2);
          box-shadow: 0px 0px 0px 1px rgba(54, 201, 169, 0.2);
}

.snake-block {
  width: calc(700px / 16);
  height: calc(700px / 16);
  -webkit-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
          box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
  background: springgreen;
  position: absolute;
}

.food-container {
  position: absolute;
  width: calc(700px / 16);
  height: calc(700px / 16);
  background: transparent;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.food {
  width: 50%;
  height: 50%;
  background: tomato;
  -webkit-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
          box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
}

.game-over {
  top: calc(43.75px * 5);
  background: lightblue;
  width: calc(43.75px * 7);
  left: calc(43.75px * 5);
  height: calc(43.75px * 2);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
          box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 1);
  font-size: 2em;
  color: red;
  font-weight: bold;
  position: absolute;
}



